<template>
<div>
    <div class="title-checkout text-center d-flex flex-column align-item-center justify-content-center">
    <h1>Checkout</h1>
    </div>
    <section class="row content-checkout">
    <ValidationObserver ref="observer" class="col-md-4">
            <form>
                <h3>Customer information</h3>
                    <div class="form-group">
                    <label for="useremail">Email</label>
                    <ValidationProvider name="Email" rules="required|email" v-slot="{ errors }">
                    <input type="email" class="form-control" name="email" id="useremail"
                        v-model="form.user.email" placeholder=" Email" required>
                    <span class="text-danger">{{ errors[0] }}</span>
                    </ValidationProvider>
                    </div>

                    <div class="form-group">
                    <label for="username"></label>
                    <ValidationProvider name="Name" rules="required|min:2" v-slot="{ errors }">
                    <input type="text" class="form-control" name="name" id="username"
                        v-model="form.user.name" placeholder="">
                    <span class="text-danger">{{ errors[0] }}</span>
                    </ValidationProvider>
                    </div>

                    <div class="form-group">
                    <label for="usertel"></label>
                    <ValidationProvider name="Phone" rules="required|numeric|min:8|max:10" v-slot="{ errors }">
                    <input type="tel" class="form-control" id="usertel" v-model="form.user.tel" placeholder="">
                    <span class="text-danger">{{ errors[0] }}</span>
                    </ValidationProvider>
                    </div>
                
                    <div class="form-group">
                    <label for="useraddress"></label>
                    <ValidationProvider name="Address" rules="required" v-slot="{ errors }">
                    <input type="text" class="form-control" name="address" id="useraddress" v-model="form.user.address" placeholder="">
                    <span class="text-danger">{{ errors[0] }}</span>
                    </ValidationProvider>
                    </div>
                
                    <div class="form-group">
                    <label for="comment"></label>
                    <textarea name="" id="comment" class="form-control" cols="10" rows="3" v-model="form.message"></textarea>
                    </div>
            </form>
    </ValidationObserver>
        <div class="col-md-8">
            <h3 class="text-center">Shopping cart</h3>
            <table class="table">
                <thead>
                    <th width="60"></th>
                    <th></th>
                    <th width="120" class="text-center"></th>
                    <th width="120"></th>
                    <th width="80"></th>
                </thead>
                <tbody>
                    <tr v-for="item in cart.carts" :key="item.id" v-if="cart.carts">
                    <td>
                    <img :src="item.product.imageUrl" class="img-fluid img-checkout" alt="img-product" style="height:60px;">
                    </td>
                    <td class="align-middle">
                        {{ item.product.title }}
                        <div class="text-success" v-if="item.coupon">
                        
                        </div> 
                    </td>
                    <td class="align-middle text-center">{{ item.qty }}</td>
                    <td class="align-middle">{{ item.final_total | currency}}</td>                    
                    <td class="align-middle">
                        <button type="button" class="btn btn-outline-danger btn-sm" @click="removeCartItem(item.id)">
                        <i class="far fa-trash-alt"></i>
                        </button>
                    </td>
                    </tr>
                </tbody>
                <tfoot>
                    <tr>
                    <td colspan="4" class="text-right"></td>
                    <td class="text-right">{{ cart.total | currency }}</td>
                    </tr>
                    <tr v-if="cart.final_total !== cart.total">
                    <td colspan="4" class="text-right text-success"></td>
                    <td class="text-right text-success">{{ cart.final_total | currency }}</td>
                    </tr>
                </tfoot>
            </table>
            <div class="input-group mb-3 input-group-sm">
                <input type="text" class="form-control" v-model="coupon_code" placeholder="">
                <div class="input-group-append">
                    <button class="btn btn-outline-secondary" type="button" @click="addCoupon">
                    
                    </button>
                </div>
            </div>
            <div class="text-center">
                <button class="btn btn-primary" @click.prevent="createOrder">Process to checkout</button>
            </div>
        </div>
    </section>
   
</div>
</template>


<script>
import $ from 'jquery';
import { extend } from 'vee-validate';
import * as rules from 'vee-validate/dist/rules';

for (let rule in rules) {
  extend(rule, rules[rule]);
}

extend('required', {
  validate: value => !!value
});
 

export default{ 
    data(){
        return{
            products:[],
            cart:{},
            coupon_code:'',
            form:{
                user:{
                    name:'',
                    email: '',
                    tel: '',
                    address: '',
                },
                message: '',
            },
        };
    },
    methods:{
        getCart(){
            const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
            const vm = this;
           
            this.$http.get(url).then((response) => {
            vm.cart = response.data.data;
            });
        },
        removeCartItem(id){
            const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart/${id}`;
            const vm = this;

            this.$http.delete(url).then(() => {
            vm.$bus.$emit("getCart");
            vm.getCart();
            
            });
        },
        addCoupon(){
            const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/coupon`;
            const vm = this;
            const coupon = {
                code: vm.coupon_code
            }
            
            this.$http.post(url, {data: coupon}).then((response) => {
            if(response.data.success){
                vm.getCart();
            }
            else{
                this.$bus.$emit('message:push', response.data.message, 'danger');
                vm.getCart();
            }
            });
        },
        async createOrder(){
            const isValid = await this.$refs.observer.validate(); // validate form 
             if (!isValid) {
                 return;
                }
            const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order`;
            const vm = this;
            const order = vm.form;
            vm.isLoading = true;

            this.$http.post(url, {data: order}).then((response) => {
                //console.log('', response);
                if(response.data.success){
                    vm.$router.push(`/customer_checkout/${response.data.orderId}`);
                    vm.isLoading = false;
                }
            });
        }
    },
    created(){
        this.getCart();
    },
}
</script>


