import Vue from 'vue';
import VueRouter from 'vue-router';
//

import Index from '@/components/index';
import Home from '@/components/Home';
import About from '@/components/About';
import Login from '@/components/admin/login';
import Dashboard from '@/components/admin/Dashboard';
import ProductList from '@/components/admin/ProductList';
import Products from '@/components/Products';
import Product from '@/components/Product';
import CheckOut from '@/components/CheckOut';
import CustomerCheckout from '@/components/CustomerCheckout';
import OrderList from '@/components/admin/OrderList';
import Coupon from '@/components/admin/Coupon';



//

Vue.use(VueRouter);

export default new VueRouter({
    routes: [
        {
            path:'*',
            redirect:'/home',
        },
        {
            name:'', //  
            path:'/',   //
            redirect:'/home',
            name: 'index',
            component:Index, //
            children: [
                {
                    path:'home',
                    name:'Home',
                    component: Home, 
                },
                {
                    path:'about',
                    name:'About',
                    component: About,
                },
                {
                    path:'products',
                    name:'Products',
                    component: Products, 
                    children: [
                        {
                            path: 'laptop',
                            name: 'Laptop',
                            components: {
                                default: Products,
                            },
                            meta: { scroll: true },
                        },
                        {
                            path: 'headphone',
                            name: 'Headphone',
                            components: {
                                default: Products,
                            },
                            meta: { scroll: true },
                        },
                        {
                            path: 'speaker',
                            name: 'Speaker',
                            components: {
                                default: Products,
                            },
                            meta: { scroll: true },
                        },
                        {
                            path: 'phone',
                            name: 'Phone',
                            components: {
                                default: Products,
                            },
                            meta: { scroll: true },
                        },
                    ]
                },
                {
                    path:'product/:id',
                    name:'Product',
                    component: Product, 
                },
                {
                    path:'checkout',
                    name:'CheckOut',
                    component: CheckOut, 
                },
                {
                    path:'customer_checkout/:orderId',
                    name:'CustomerCheckout',
                    component: CustomerCheckout, 
                },
            ]
        },
        {
            path:'/login',
            name:'login',
            component: Login,
        },
        {
            path:'/admin',
            name: 'Dashboard',
            component: Dashboard,
            children: [
                {
                    path:'productlist',
                    name:'ProductList',
                    component: ProductList, 
                    meta: { requiresAuth: true },
                },
                {
                    path:'coupon',
                    name:'Coupon',
                    component: Coupon, 
                    meta: { requiresAuth: true },
                },
                {
                    path:'orderlist',
                    name:'OrderList',
                    component: OrderList, 
                    meta: { requiresAuth: true },
                },
            ]
        },
    ]
});


