<template>
<div>
  <div class="title-checkout text-center d-flex flex-column align-item-center justify-content-center">
  <h1>Confirmation</h1>
  </div>
  <div class="my-5 row justify-content-center">
    <form class="col-md-6" >
      <table class="table">
        <thead>
          <th width="120"></th>
          <th class="text-left"></th>
          <th width="120"></th>
          <th width="120" class="text-right"></th>
        </thead>
        <tbody>
          <tr v-for="item in order.products" :key="item.id">
            <td class="align-middle"><img :src="item.product.imageUrl" class="img-fluid" alt="img-product" style="height:60px; width:60px;"></td>
            <td class="align-middle text-left">{{ item.product.title }}</td>
            <td class="align-middle">{{ item.qty }}</td>
            <td class="align-middle text-right">{{ item.final_total }}</td>
          </tr>
        </tbody>
        <tfoot>
          <tr>
            <td colspan="3" class="text-right"></td>
            <td class="text-right">${{ order.total }}</td>
          </tr>
        </tfoot>
      </table>

      <table class="table">
        <tbody>
          <tr>
            <th width="100">Email</th>
            <td>{{ order.user.email }}</td>
          </tr>
          <tr>
            <th></th>
            <td>{{ order.user.name }}</td>
          </tr>
          <tr>
            <th></th>
            <td>{{ order.user.tel }}</td>
          </tr>
          <tr>
            <th></th>
            <td>{{ order.user.address }}</td>
          </tr>
          <tr>
            <th></th>
            <td>
              <span v-if="!order.is_paid"></span>
              <span v-else class="text-success"></span>
            </td>
          </tr>
        </tbody>
      </table>
        <div class="text-right" v-if="order.is_paid === false">
          <button class="btn btn-primary" @click.prevent="payOrder">Place order</button>
        </div>
    </form>
  </div>
</div>
</template>

<script>
export default{
    data(){
        return{
            order:{
                user:{},
            },
            orderId:'',
        };
    },
    methods:{
        getOrder(){
            const vm = this;
            const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order/${vm.orderId}`;            
            vm.isLoading = true;

            this.$http.get(url).then((response) => {
            vm.order = response.data.order;
            vm.isLoading = false;
            });
        },
        toCheckout(){
            this.$router.push(`/CheckOut`);
        },
        payOrder() {
            const vm = this;
            const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/pay/${vm.orderId}`;
            vm.isLoading = true;
            
            this.$http.post(url).then((response) => {
                console.log(response);
                if (response.data.success) {
                vm.getOrder();
                vm.$bus.$emit("getCart");
                }
                vm.isLoading = false;
            });
        },
    },
    created(){
        this.orderId = this.$route.params.orderId;        //console.log(this.orderId);
        this.getOrder();
    },
};
</script>


